import type { Decorator, Meta, StoryObj } from '@storybook/web-components';
import { html } from 'lit';
import type { OAppBar } from '../components/o-app-bar/o-app-bar';

export default {
	title: 'Components/App Bar',
	argTypes: {},
	args: {}
} as Meta;

// Custom decorator to increase width
const customHeightDecorator: Decorator = (story) => {
	return html`<div style="height: 200px;">${story()}</div>`;
};

const Template: StoryObj<OAppBar> = {
	decorators: [customHeightDecorator],
	parameters: {
		layout: 'fullscreen',
		backgrounds: {
			default: 'light' // Set light as the default background
		},
		docs: {
			source: {
				transform: (_, storyContext) => {
					const {} = storyContext;

					// Create dynamic code example using args
					return;
				}
			}
		}
	},
	render: () => html`
		<o-app-bar>
			<div slot="right"><o-avatar></o-avatar></div>
		</o-app-bar>
	`
};

export const Default = {
	...Template,
	render: () => html`
		<o-app-bar>
			<div slot="right"><o-avatar></o-avatar></div>
		</o-app-bar>
	`,
	parameters: {
		layout: 'fullscreen',
		docs: {
			description: {
				story: 'The default App Bar component'
			}
		}
	}
};
